* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}
.manyListTasks {
  display: flex;
  flex-flow: row wrap;
}
.listTaskItem {
  position: relative;
}

.addNewGroup {
  position: absolute;
  top: 8px;
  left: 47%;
}

.addNewGroup i.fa.fa-plus-circle {
  font-size: 40px;
  color: green;
  cursor: pointer;
}
.listTaskItem {
  flex: 0 1 25%;
  padding: 10px;
}

.toDoListOut {
  margin: 20px auto;
  max-width: 400px;
  background: #eeeeee;
  padding: 20px;
}
.toDoListForm {
  display: flex;
  flex-flow: row wrap;
  align-items: center;
  justify-content: space-between;
}

.toDoListFormInput {
  flex: 1 1 auto;
}

.toDoListFormInput input {
  width: 100%;
  padding: 6px 10px;
}

.toDoListFormPlus i {
  background: #009688;
  color: #fff;
  padding: 7px;
  cursor: pointer;
}
.listTasks li {
  list-style: none;
}

.listTasks ul {
  padding: 9px;
}
