li.listItem {
  display: flex;
  flex-flow: row wrap;
  justify-content: space-between;
  padding: 10px 5px;
}
li.listItem.done {
  text-decoration: line-through;
  color: green;
}
span.taskName {
  flex: 1 1 auto;
  padding-left: 9px;
}

span.completedTaskButton i {
  cursor: pointer;
}

span.deleteTaskButton i {
  cursor: pointer;
}
li.listItem.isCompleted {
  text-decoration: line-through;
}
span.deleteTaskButton i:hover {
  color: red;
  transition: all 0.5s ease;
}

span.completedTaskButton i:hover {
  color: green;
  transition: all 0.5s ease;
}

li.listItem:hover {
  transition: all 0.5s ease;
  background: #009688;
  color: #fff;
}

.listTasks ul {
  padding: 0;
}
